import React from "react"
import ReactPlayer from 'react-player'
import { MdPlayArrow, MdPause, MdReplay, MdVisibility, MdVisibilityOff } from 'react-icons/md'
import ProgressBar from 'react-bootstrap/ProgressBar'
import Overlaid from './overlaid'

class Ytplayer extends React.Component {
  state = {
    visible: false,
    muted: true,
    playing: true,
    playedSeconds: 0,
  }

  ref = player => {
    this.player = player
  }

  play = () => {
    const { muted } = this.state
    const { video } = this.props
    if(muted) this.player.seekTo(video.startSeconds)
    this.setState({ playing: true, muted: false })
  }

  pause = () => {
    this.setState({ playing: false, muted: false })
  }

  replay = () => {
    this.player.seekTo(this.props.video.startSeconds)
    this.play()
  }

  onEnded = () => {
    this.player.seekTo(this.props.video.startSeconds)
  }

  onProgress = state => {
    const { video, duration } = this.props
    const { playedSeconds } = this.state
    this.setState(state)
    if (playedSeconds > video.startSeconds + duration) {
      this.player.seekTo(this.props.video.startSeconds)
    }
  }

  onPause = () => {
    this.setState({ playing: false })
  }

  onStart = () => {
    this.setState({ playing: true })
  }

  toggleVisibility = () => {
    this.setState({ visible: !this.state.visible })
  }

  render() {
    const { video, duration } = this.props
    const { playing, visible, muted, playedSeconds } = this.state
    return (
      <>
      { visible ?
          <Overlaid>
            <h4 className="visibility-control" onClick={this.toggleVisibility}>
              <MdVisibility/>&nbsp;Video Caption
            </h4>
          </Overlaid>
        : <Overlaid>
            <h4 className="visibility-control" onClick={this.toggleVisibility}><MdVisibilityOff/>
              &nbsp;Audio Caption
            </h4>
          </Overlaid>
      }
      <div className="btn-group d-flex" role="group">
        { playing && !muted ?
          <button type="button" className="btn btn-sm btn-warning w-100" onClick={this.pause}><MdPause /></button>
          : <button type="button" className="btn btn-sm btn-success w-100" onClick={this.play}><MdPlayArrow /></button>
        }
        <ProgressBar
          id="progress"
          now={!muted && (playedSeconds - video.startSeconds) * 10}
          animated
        />
      </div>
      <h5 style={{display: !visible ? 'block' : 'none'}}>
        {video.audioCaption}
      </h5>
      <h5 style={{display: visible ? 'block' : 'none'}}>
        {video.videoCaption}
      </h5>
      <ReactPlayer
        ref={this.ref}
        style={
          {
            marginBottom: '1rem',
            display: visible ? 'block' : 'none',
          }
        }
        width='100%'
        playing={playing}
        muted={muted}
        url={`https://www.youtube.com/watch?v=${video.id}`}
        config={{
          youtube: {
            preload: true,
            playerVars: {
              showInfo: 0,
              autoplay: 1,
              controls: 0,
              enablejsapi: 1,
              rel: 0,
              start: video.startSeconds,
              end: video.startSeconds + duration,
              modestbranding: 1,
            },
          }
        }}
        onEnded={this.onEnded}
        onProgress={this.onProgress}
        onPlay={this.onPlay}
        onPause={this.onPause}
      />
    </>
    )
  }
}

export default Ytplayer
